
.shopcar {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 1rem;
  background-color: #141d27;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1000;
}
.shopcar .carleft {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 6rem;
  height: 1rem;
  background-color: #141d27;
}
.shopcar .carleft .num {
  width: 0.4rem;
  height: 0.25rem;
  border-radius: 0.4rem;
  background-color: red;
  position: fixed;
  left: 0.9rem;
  bottom: 0.9rem;
  color: #fff;
  text-align: center;
  font-size: 0.16rem;
}
.shopcar .carleft .cost {
  width: 1.2rem;
  font-size: 0.38rem;
  line-height: 0.5rem;
  color: #919396;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  border-right: 2px solid #2b333b;
}
.shopcar .carleft .ship {
  font-size: 0.32rem;
  color: #919396;
  line-height: 1rem;
  padding-left: 0.3rem;
}
.shopcar .carleft .car {
  width: 1rem;
  padding: 0.3rem;
}
.shopcar .carleft .car .cartotal {
  width: 1rem;
  height: 1rem;
  border-radius: 1rem;
  background-color: #141d27;
  position: fixed;
  bottom: 0.2rem;
  text-align: center;
  line-height: 1rem;
}
.shopcar .carleft .car .cartotal .caricon {
  width: 0.8rem;
  height: 0.8rem;
  position: fixed;
  bottom: 0.3rem;
  border-radius: 0.8rem;
  background-color: #2b343c;
  margin-left: 0.1rem;
  line-height: 0.7rem;
}
.shopcar .carleft .car .cartotal .caricon.iconcol {
  background-color: #00a0dc;
}
.shopcar .carleft .car .cartotal .caricon .icon-gouwuche {
  font-size: 0.48rem;
  color: #80858a;
}
.shopcar .carleft .car .cartotal .caricon .icon-gouwuche.iconcol2 {
  color: #fff;
}
.shopcar .carright {
  width: 2.1rem;
  height: 1rem;
  background-color: #2b333b;
  text-align: center;
  line-height: 1rem;
  color: #979a9c;
}
.shopcar .carright p {
  font-size: 0.24rem;
  font-weight: 700;
}
.shopcar .carright.not-enough {
  background: red;
  color: #979a9c;
}
.shopcar .carright.enough {
  color: #fff;
  background-color: green;
}
.shopcar .carfoods {
  width: 100%;
  background-color: rgba(7, 17, 27, 0.8);
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.shopcar .carfoods .carbody {
  width: 100%;
  position: fixed;
  z-index: 90;
  bottom: 0;
  background-color: white;
}
.shopcar .carfoods .carbody .car {
  width: 100%;
  height: 0.8rem;
  background-color: #f3f5f7;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.shopcar .carfoods .carbody .car .cartxt {
  font-size: 0.28rem;
  font-weight: 200;
  color: #07111b;
  line-height: 0.8rem;
  padding-left: 0.3rem;
}
.shopcar .carfoods .carbody .car .clear {
  padding-left: 5.5rem;
  font-size: 0.24rem;
  color: #00a0dc;
  line-height: 0.8rem;
}
.shopcar .carfoods .carbody .foodlist {
  max-height: 6rem;
  overflow: hidden;
}
.shopcar .carfoods .carbody .foodlist .listcon {
  max-height: 100rem;
}
.shopcar .carfoods .carbody .foodlist .listcon .listleft {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 0.66rem;
  margin: 0.3rem;
  font-size: 0.28rem;
  color: #07111b;
  line-height: 0.48rem;
  border-bottom: 1px solid  #ddd;
}
.shopcar .carfoods .carbody .foodlist .listcon .listleft .foodname {
  width: 4.5rem;
}
.shopcar .carfoods .carbody .foodlist .listcon .listleft .price {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 2rem;
  text-align: center;
}
.shopcar .carfoods .carbody .foodlist .listcon .listleft .price .pri {
  width: 0.4rem;
  font-size: 0.2rem;
  font-weight: 700;
  line-height: 0.48rem;
  color: #f01414;
}
.shopcar .carfoods .carbody .foodlist .listcon .listleft .price .add {
  margin-left: 0.2rem;
}
.shopcar .carfoods .carbody .space {
  width: 100%;
  height: 1.4rem;
}



